export {FilePicker} from './elements/filepicker/file-picker.js';
import {lang} from './utils/lang/lang.js';
import {registerPlaceholders} from './utils/ph/register.js';
import {Addon_Docs} from './platforms/docs.js'
import {Addon_Web} from './platforms/web.js'

registerPlaceholders();
document.addEventListener('DOMContentLoaded', () => {

    const platform = document.body.dataset.platform;
    const page = document.body.dataset.page;
    alert(platform);

    //console.log('Platform:', platform);
    //console.log('Page:', page);

    switch (platform) {
        case 'docs':
            Addon_Docs.init(page);
            break;
        case 'web':
            Addon_Web.init(page);
            break;
    }

    lang.updateUI();
});