import {ModelDataSource} from '../../../../models/model.datasource.js';
import {collectRefs} from '../../../../utils/utils.html.js';

export const PanelDataSource = {

    //-------------------------------------------------------------------
    //--- init()
    //-------------------------------------------------------------------
    init() {
        this.panel = document.querySelector('#panel-datasource');        
        this.panel.addEventListener('click', this.handleClick.bind(this));
        this.refs = collectRefs(this.panel);
    },

    //-------------------------------------------------------------------
    //--- handleClick()
    //-------------------------------------------------------------------
    handleClick(e) {
        const action = e.target.closest('[data-action]')?.dataset.action;
        if (!action) return;
        this.runAction(action);
    },

    //-------------------------------------------------------------------
    //--- runAction()
    //-------------------------------------------------------------------
    runAction(action) {
        const map = {
            'add-snippet': () => console.log('add-snippet')
        };
        map[action]?.();
    }
}